import React from "react"
import {Helmet} from "react-helmet"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Search from "../components/search"
import {Link} from "gatsby"
import cari from "../css/cari.module.css"
const Cari = ({data}) => {
  // const { data } = this.props

  const posts = data.allCockpitPost.edges
  
  return (

  <Layout>
    
    <Helmet>
    <html lang="id" />


    </Helmet>
    <SEO title="404 | muhzulzidan" lang="id" />

    <h1>TIDAK DI TEMUKAN</h1>
    <h3>tekan logo di sudut kiri atas </h3>
    <h3>atau cari di bawah ini</h3>
    
    <Search></Search>
    <br/>
    <h2>Daftar Semua Postingan</h2> 
    {posts.map(({ node,  }) => {
      return (
      <div>
      <Link to={`${node.title.slug}`}
      className={cari.a1}
      key={node.id}>
        <h4>{node.title.value}</h4>
      </Link>
      </div>
      

      )
    })}
    
  </Layout>
)
}

export default Cari
export const pageQuery = graphql`
  query {
    site { 
        siteMetadata {
      title
    } 
    }
    allCockpitPost(filter: {lang: {eq: "id"}, published: {value: {eq: true}}}, sort: {order: DESC, fields: date___value}, ) {
      edges { 
      node {
        id
        title { 
          value
          slug
        }
      }
    } 
    }
  }
`;